<template>
  <div>
    <nav aria-label="Page navigation example" v-if="pageData.total_pages != 0">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pageData.has_pre }">
          <a class="page-link" href="#" aria-label="Previous"
          @click.prevent="changePage(pageData.current_page - 1)">
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pageData.total_pages" :key="page"
        :class="{'active' : pageData.current_page === page}">
          <a class="page-link" href="#" @click.prevent="changePage(page)">{{ page }}</a>
        </li>
        <li class="page-item" :class="{'disabled': !pageData.has_next }">
          <a class="page-link" href="#" aria-label="Next"
          @click.prevent="changePage(pageData.current_page + 1)">
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
// :pageData="{ 頁碼資訊 }"
// @emitPages="更新頁面事件"
export default {
  name: 'pagination',
  props: {
    pageData: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  methods: {
    changePage (page) {
      // 每次換頁都回到最上
      window.scrollTo({ top: 0 })
      this.$emit('emitPages', page)
    }
  }
}
</script>
